<template>
  <x-crud :loading="loading">
    <x-form v-model="formData" :values="formData" @submit="onsubmit">
      <a-row :gutter="[24, 24]">
        <a-col
          :md="{ span: 12, offset: 6 }"
          :lg="{ span: 12, offset: 6 }"
          :xl="{ span: 12, offset: 6 }"
          class="col-form"
        >
          <x-input
            label="Период, начало"
            name="start_achievement"
            :options="['2021', '2020', '2019', '2018', '2017', '2016', '2015']"
            type="select"
            :disabled="disabled"
            validation="required"
          ></x-input>
          <x-input
            label="Период, конец"
            name="finish_achievement"
            :options="['2021', '2020', '2019', '2018', '2017', '2016', '2015']"
            type="select"
            :disabled="disabled"
            validation="required|comparison"
            :validation-rules="{
              comparison: ({ value }) => {
                return (
                  Number(formData.finish_achievement) >=
                  Number(formData.start_achievement)
                )
              },
            }"
            :validation-messages="{
              comparison: 'Год окончания периода меньше начала',
            }"
          ></x-input>
          <div class="crud-form-button">
            <a-button type="primary" block html-type="submit">
              {{ getButtonAction }}
            </a-button>
          </div>
        </a-col>
      </a-row>
    </x-form>
  </x-crud>
</template>

<script>
import crudMixin from '@/mixins/crudMixin'
import { PERIOD } from '@/helpers/constants'

export default {
  props: ['action', 'id'],
  mixins: [crudMixin],
  data() {
    return {
      formData: {
        start_achievement: null,
        finish_achievement: null,
      },
      entityName: PERIOD,
    }
  },
  methods: {
    async onsubmit() {
      if (this.action === 'read') {
        this.$router.push(`/${PERIOD}/edit/${this.id}`)
        return
      }
      this.formData.name = `${this.formData.start_achievement}-${this.formData.finish_achievement}`
      await this.saveEntity(this.formData)
    },
  },
  async created() {
    if (this.action !== 'create') await this.getEntity(this.id)
    this.loading = false
    console.log(this.action, this.id, this.disabled)
  },
}
</script>

